import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { bindActionCreators } from 'redux';
import * as actions from '../../store/actions/details';
import Header from '../themes/common/Header';
import { getPostId } from '../../store/helpers/localStorage'
import ispdacheck from '../../assets/images/ispdacheck.svg'
import { baseURL } from '../../core/constants/baseURL'
import profile_phone from '../../assets/images/profile_phone2x.png'
import './style.modules.scss';

export class AdvertDetails extends Component {

    constructor() {
        super();
        this.advertid = getPostId()
    }

    componentDidMount = () => {
        this.props.actions.getAdvertDetails(this.advertid);
    }

    render() {
        const { data } = this.props.info

        return (
            <Fragment >
                <Header className='menu_fix' fix="false" />
                {data ? <div className="details_wrapper">
                    <div className="details_list" >
                        {/* Head */}
                        <div className='details_list_header'><span>  </span></div>
                        <div className="details_main_info">
                            <div className="details_main_info_head">
                                <div className="details_main_info_head_contain">
                                    <span className="details_main_info_head_contain_title">{data.post.name}</span>
                                    <span className="details_main_info_head_contain_price">{data.post.price} </span>
                                </div>
                                <div className="details_main_info_head_image"><img alt="" style={{ height: '100%', width: '100%' }} src={baseURL + data.post.image}></img></div>
                            </div>

                            {/* Main Body */}
                            <div className="details_main_info_body">

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list">  </label>
                                    <span className="details_values_list">{data.post.legal_form}</span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list">   </label>
                                    <span className="details_values_list">{data.post.kved_name}</span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list"> </label>
                                    {/* {Object.values(item)[0].map((elem, index) => {
                                        return (<ul key={index + 'a'}>
                                            <li style={{ marginBlockEnd: '2%' }}>{`${index + 1}. ${elem}`}</li>
                                        </ul>)
                                    })}*/}

                                    <span className="details_values_list list"><span style={{ color: 'grey' }}>   </span></span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list">  </label>
                                    <span className="details_values_list"><span className="place_advert_detail" >{`${data.post.region},  ${data.post.city} `}</span></span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list">  </label>
                                    <span className="details_values_list">{data.post.registered_at}</span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list"> </label>
                                    <span className="details_values_list">{data.post.tax_form}</span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list"> </label>
                                    <span className="details_values_list">
                                        {data.post.pda ? <span>   <img src={ispdacheck} /></span>
                                            : <span>   </span>}
                                    </span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list"></label>
                                    <span className="details_values_list">{data.post.tax_form || <span style={{ color: 'grey' }}>   </span>}</span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list"> </label>
                                    <span className="details_values_list">{data.post.have_activity === true ? '/  ' :
                                        data.post.have_activity === false ? ' /  ' : <span style={{ color: 'grey' }}>   </span>}</span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list">  </label>
                                    <span className="details_values_list">{data.post.no_debt === true ? '' :
                                        data.post.no_debt === false ? '' : <span style={{ color: 'grey' }}>   </span>}</span>
                                </div>

                                <div className="details_body_item_list" >
                                    <label className="details_labels_list"> </label>
                                    <span className="details_values_list" style={{ fontWeight: '800' }}> {data.post.capital || <span style={{ color: 'grey' }}>   </span>} </span>
                                </div>

                                {/* footer */}

                            </div>
                            <div className="details_main_info_footer">
                                <label className="details_labels_list "> /  </label>
                                <span className="details_values_list" style={{ paddingLeft: '2%' }}> {data.post.owner_data}</span>
                            </div>
                        </div>
                    </div>
                    <div className="details_info" >
                        <div className='details_list_header info_head'><span>  </span></div>
                        <div className="details_info_main_contain ">
                            <span style={{ fontWeight: 'bold', fontSize: '25px' }}>{data.author.first_name}</span>
                            <span style={{ marginBlockEnd: '5%' }}><img style={{ width: '1.3em', paddingRight: '5%' }} src={profile_phone} />{data.author.phone}</span>
                            {/* <span style={{marginBlockEnd:'5%'}}>email</span> */}
                        </div>
                    </div>
                </div> : <p className="results_preloader">...</p>}
            </Fragment>
        );
    }
}

export default connect(
    (state) => ({
        info: state.details.info
    }),
    dispatch => ({
        actions: bindActionCreators(actions, dispatch)
    })
)(AdvertDetails);